section {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100vh;
	width: 100%;
	border-bottom: 1px solid lightgrey;
}

section:nth-child(even) {
	flex-direction: row-reverse;
}

section img,
section div {
	height: auto;
	width: 100%;
}

section img {
	width: 50%;
}

section div {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	padding: 1em;
}

.firstmodal {
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
  
	border: 1px solid #ccc;
	background: #fff;
	overflow: auto;
	border-radius: 4px;
	outline: none;
	padding: 20px;
  }
  
  .firstoverlay {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.75);
  }

  .secondmodal {
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
  
	border: 1px solid #ccc;
	background: #fff;
	overflow: auto;
	border-radius: 4px;
	outline: none;
	padding: 20px;
  }
  
  .secondoverlay {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background-color: rgba(0, 0, 0, 0.75);
  }
  
  .ReactModal__Overlay {
	opacity: 0;
	transition: opacity 500ms ease-in-out;
  }
  
  .ReactModal__Overlay--after-open {
	opacity: 1;
  }
  
  .ReactModal__Overlay--before-close {
	opacity: 0;
  }

@media screen and (max-width: 900px) {
	section,
	section:nth-child(even) {
		flex-direction: column;
	}

	section img {
		height: auto;
		width: auto;
		max-width: 100vw;
	}
}